.nav {
    display: inline-block;
    background-color: #090062;
    border: 3px solid white;
    border-radius: 10px;
    margin: 20px ;
    width: 1400px;
    height: 60px;
    text-align: center;
}

.item {
    display: inline-block;
    margin: 0 30px;
    font-size: 20px;
    font-weight: bold;
    padding: 15px 10px;
}

.item a {
    color: white;
}

.active {
    color: darkviolet;
    font-style: italic;
    border: red 3px solid;
}